import { useCallback } from 'react';
export function useOnUpdateMetadata(setStoredTables, store, selectedTableName) {
    return useCallback(async (meta)=>{
        const _table = await store.get(selectedTableName);
        _table.metadata = meta;
        store.delete(_table.id);
        store.set(_table);
        const _storedTables = await store.toMap();
        setStoredTables(_storedTables);
    }, [
        store,
        selectedTableName,
        setStoredTables
    ]);
}
