export function useColumnRecordDelete(step, onChange) {
    return useCallback((column)=>{
        const internal = step;
        const args = {
            ...internal.args
        };
        delete args.columns[column];
        onChange && onChange({
            ...step,
            args: {
                ...internal.args,
                ...args
            }
        });
    }, [
        step,
        onChange
    ]);
}
export function useLoadTable(id, table, store) {
    const [tbl, setTable] = useState();
    const handleTableLoad = useCallback((container)=>setTable(container.table)
    , [
        setTable
    ]);
    useEffect(()=>{
        const fn = async (n, s)=>{
            try {
                s.listen(n, handleTableLoad);
                const c = await s.get(n);
                setTable(c.table);
            } catch (e) {
            // swallow the error - we may try to request async before the table is registered
            // it'll get picked up later by the listener
            // TODO: should we only listen if it fails at first?
            }
        };
        // if a table already exists, use it directly
        // TODO: should we set it in the store also?
        // the expectation here is that a table will be provided if the step component is used directly without a builder
        // interface that is managing a pipeline
        if (table) {
            setTable(table);
        } else if (id && store) {
            fn(id, store);
        }
        return ()=>{
            id && store && store.unlisten(id);
        };
    }, [
        id,
        table,
        store,
        handleTableLoad
    ]);
    return tbl;
}
export function useIntersection(element, rootMargin) {
    const [isVisible, setState] = useState(false);
    useEffect(()=>{
        const observer = new IntersectionObserver(([entry])=>{
            setState(entry?.isIntersecting ?? false);
        }, {
            rootMargin
        });
        element && observer.observe(element);
        return ()=>element && observer.unobserve(element)
        ;
    }, [
        element,
        rootMargin
    ]);
    return isVisible;
}
export function useColumnType(table, column) {
    return useMemo(()=>{
        if (!table || !column) {
            return DataType.Unknown;
        }
        return columnType(table, column);
    }, [
        table,
        column
    ]);
}
