export function useDeleteConfirm(onDelete) {
    const [deleteArg, setDeleteArg] = useState();
    const [isDeleteModalOpen, { toggle: toggleDeleteModalOpen  }] = useBoolean(false);
    const onDeleteClicked = useCallback((args)=>{
        setDeleteArg(args);
        toggleDeleteModalOpen();
    }, [
        toggleDeleteModalOpen,
        setDeleteArg
    ]);
    const onConfirmDelete = useCallback(()=>{
        onDelete && onDelete(deleteArg);
        toggleDeleteModalOpen();
    }, [
        toggleDeleteModalOpen,
        deleteArg,
        onDelete
    ]);
    return {
        isDeleteModalOpen,
        onConfirmDelete,
        toggleDeleteModalOpen,
        onDeleteClicked
    };
}
