export function useCreateTableName(store) {
    const verifyTableName = useCallback((name)=>{
        return store ? store.list().includes(name) : false;
    }, [
        store
    ]);
    return useCallback((name)=>{
        const originalName = name.replace(/( \(\d+\))/, '');
        let derivedName = originalName;
        let count = 1;
        while(verifyTableName(derivedName)){
            derivedName = `${originalName} (${count})`;
            count++;
        }
        return derivedName;
    }, [
        verifyTableName
    ]);
}
export function useCreateColumnName() {
    const verifyColumnName = useCallback((name, columnNames)=>{
        return columnNames.includes(name);
    }, []);
    return useCallback((name, columnNames)=>{
        const originalName = name.replace(/( \(\d+\))/, '');
        let derivedName = originalName;
        let count = 1;
        while(verifyColumnName(derivedName, columnNames)){
            derivedName = `${originalName} (${count})`;
            count++;
        }
        return derivedName;
    }, [
        verifyColumnName
    ]);
}
export function useFormatedColumnArg() {
    return useCallback((stepArgs, newName = 'New column')=>{
        const args = stepArgs;
        Object.keys(args).forEach((x)=>{
            if (x === 'to' && !isArray(args[x])) args[x] = newName;
        });
        return args;
    }, []);
}
export function useFormatedColumnArgWithCount() {
    const createColumnName = useCreateColumnName();
    return useCallback((step, columnNames)=>{
        let args = step.args;
        Object.keys(args).forEach((x)=>{
            if (x === 'to' && !isArray(args[x])) {
                const newColumnName = createColumnName(args[x], columnNames);
                args = {
                    ...args,
                    [x]: newColumnName
                };
            }
        });
        return args;
    }, [
        createColumnName
    ]);
}
