import { BooleanComparisonOperator, DataType, NumericComparisonOperator, StringComparisonOperator, types } from '@data-wrangling-components/core';
import { useMemo } from 'react';
export function useIsEmpty(criterion) {
    return useMemo(()=>{
        const { operator  } = criterion;
        return operator === NumericComparisonOperator.IsEmpty || operator === NumericComparisonOperator.IsNotEmpty || operator === StringComparisonOperator.IsEmpty || operator === StringComparisonOperator.IsNotEmpty || operator === BooleanComparisonOperator.IsTrue || operator === BooleanComparisonOperator.IsFalse || operator === BooleanComparisonOperator.IsEmpty || operator === BooleanComparisonOperator.IsNotEmpty;
    }, [
        criterion
    ]);
}
export function usePlaceholderText(type) {
    return useMemo(()=>{
        switch(type){
            case DataType.String:
                return 'text or column';
            case DataType.Number:
                return 'number or column';
            case DataType.Boolean:
                return 'boolean or column';
        }
    }, [
        type
    ]);
}
export function useColumnTyping(table, column) {
    const tps = useMemo(()=>{
        return table ? types(table) : {};
    }, [
        table
    ]);
    const type = useMemo(()=>tps[column]
    , [
        tps,
        column
    ]);
    const columnFilter = useMemo(()=>{
        return (name)=>tps[name] === type
        ;
    }, [
        tps,
        type
    ]);
    return {
        type,
        columnFilter
    };
}
