import { useMemo } from 'react';
import { selectStepComponent, withInputColumnDropdown, withOutputColumnTextfield } from '../../index.js';
export function useHandleStepArgs(step, hideInputColumn, hideOutputColumn) {
    const Component = useMemo(()=>step ? selectStepComponent(step) : null
    , [
        step
    ]);
    const WithColumns = useMemo(()=>{
        if (Component) {
            let comp = Component;
            if (!hideInputColumn) {
                comp = withInputColumnDropdown()(comp);
            }
            if (!hideOutputColumn) {
                comp = withOutputColumnTextfield()(comp);
            }
            return comp;
        }
    }, [
        Component,
        hideInputColumn,
        hideOutputColumn
    ]);
    return WithColumns;
}
