import { op } from 'arquero';
import { useCallback, useMemo } from 'react';
export function useColumnValues(internal, table) {
    return useMemo(()=>{
        const { column  } = internal.args;
        if (!table || !column) {
            return [];
        }
        const args = {
            [column]: op.array_agg_distinct(column)
        };
        const rollup = table.orderby(column).rollup(args);
        return rollup.get(column, 0);
    }, [
        table,
        internal
    ]);
}
export function useHandleRecodeChange(internal, onChange) {
    return useCallback((previous, oldValue, newValue)=>{
        const map = {
            ...internal.args.map
        };
        delete map[previous];
        map[oldValue] = newValue;
        onChange && onChange({
            ...internal,
            args: {
                ...internal.args,
                map
            }
        });
    }, [
        internal,
        onChange
    ]);
}
export function useRecodeDelete(step, onChange) {
    return useCallback((value)=>{
        const args = {
            ...step.args
        };
        delete args.map[value];
        onChange && onChange({
            ...step,
            args: {
                ...step.args,
                ...args
            }
        });
    }, [
        step,
        onChange
    ]);
}
// find the next value from the table to suggest
function next(internal, values) {
    return values.find((value)=>{
        if (!internal.args.map) {
            return true;
        }
        return internal.args.map[value] === undefined;
    });
}
export function useHandleAddButtonClick(internal, values, onChange) {
    return useCallback(()=>{
        const nextValue = next(internal, values);
        if (nextValue !== undefined) {
            // could be a 0 or false...
            onChange && onChange({
                ...internal,
                args: {
                    ...internal.args,
                    map: {
                        ...internal.args.map,
                        [nextValue]: nextValue
                    }
                }
            });
        }
    }, [
        internal,
        values,
        onChange
    ]);
}
export function useDisabled(internal, values) {
    if (values.length === 0 || !internal.args.column) {
        return true;
    }
    return values.length === Object.keys(internal.args.map || {}).length;
}
