import { useThematic } from '@thematic/react';
import merge from 'lodash-es/merge.js';
import { useMemo } from 'react';
/**
 * Defaults modal to have a slight border that is adapted for theme
 * @param styles
 * @returns
 */ export function useModalStyles(styles, includeGuidance = false, hasSelectedStep = false) {
    const theme = useThematic();
    return useMemo(()=>{
        return merge({
            root: {
                border: `1px solid ${theme.application().faint().hex()}`,
                width: includeGuidance ? 800 : 360,
                maxHeight: 580
            }
        }, styles);
    }, [
        theme,
        styles,
        includeGuidance,
        hasSelectedStep
    ]);
}
