import { useBoolean } from '@fluentui/react-hooks';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useOnDuplicateStep, useOnEditStep } from './index.js';
export function useManageSteps(type, store, table, onSave) {
    const [step, setStep] = useState();
    const [stepIndex, setStepIndex] = useState();
    const [isTransformModalOpen, { setTrue: showTransformModal , setFalse: hideTransformModal  }, ] = useBoolean(false);
    const onDismissTransformModal = useCallback(()=>{
        hideTransformModal();
        setStep(undefined);
        setStepIndex(undefined);
    }, [
        setStep,
        setStepIndex,
        hideTransformModal
    ]);
    const onEditClicked = useOnEditStep(setStep, setStepIndex, showTransformModal);
    const onCreate = useCallback((_step)=>{
        onSave && onSave(_step, stepIndex);
        onDismissTransformModal();
    }, [
        onSave,
        onDismissTransformModal,
        stepIndex
    ]);
    const onDuplicateClicked = useOnDuplicateStep(type, store, table, onSave);
    const addStepButtonId = useMemo(()=>`button-${Math.round(Math.random() * 3)}`
    , []);
    const [editorTarget, setEditorTarget] = useState(addStepButtonId);
    useEffect(()=>{
        if (stepIndex !== undefined) {
            setEditorTarget(`.step-card-${stepIndex}`);
        } else {
            setEditorTarget(`#${addStepButtonId}`);
        }
    }, [
        addStepButtonId,
        stepIndex
    ]);
    const onStartNewStep = useCallback(()=>{
        showTransformModal();
    }, [
        showTransformModal
    ]);
    return {
        step,
        onDuplicateClicked,
        onDismissTransformModal,
        onEditClicked,
        onCreate,
        isTransformModalOpen,
        onStartNewStep,
        addStepButtonId,
        editorTarget
    };
}
